import React, { useState } from 'react'
import { PRODUCT_LIST } from '../ProductList'
import Product from './Product'
import ShoppingCartCheckoutIcon from '@mui/icons-material/ShoppingCartCheckout';
import { Popover } from '@mui/material';
import CartItems from './CartItems';

const Products = () => {
    const [cartItems, setCartItems] = useState([]);
    const [showCart, setShowCart] = useState(false);

    const addFunction = (id) => {
        setCartItems((prev) => [...prev, id])
    };
    const onClose = () => {
        setShowCart(false)
    }

    const removeFunction = (index) => {
        console.log("remove", index);
        cartItems.splice(index, 1);
        setCartItems(cartItems);
        console.log("removed", cartItems);
        onClose();
    }
    let lists = PRODUCT_LIST.map(item => <Product key={item.id} laptop={item} addFunction={addFunction} />)


    return (
        <div>
            <div style={{
                position: "relative",
                border: "1px solid black",
                width: "55px",
                marginTop: "20px",
                paddingTop: "5px",
                cursor: "pointer"
            }} onClick={() => { setShowCart(true); console.log("Clicked", cartItems); }}>
                <ShoppingCartCheckoutIcon fontSize='large' />
                <div style={{
                    position: "absolute",
                    top: "0px",
                    bottom: "0",
                    left: "35px",
                    color: "white",
                    width: "20px",
                    height: "20px",
                    backgroundColor: "red",
                    borderRadius: "50%",
                    alignItems: "center",
                    padding: "0"

                }}>
                    {cartItems.length}
                </div>

            </div>


            <div style={{
                display: "flex",
                flexWrap: "wrap",
                gap: "20px",
                margin: "10px",
                padding: "5px",
                alignItems: "center"
            }}>

                {lists}

            </div>
            {cartItems.length ? <Popover
                anchorReference="anchorPosition"
                anchorPosition={{ top: 0, left: 0 }}
                anchorOrigin={{
                    vertical: 'top',
                    horizontal: 'left',
                }}
                transformOrigin={{
                    vertical: 'top',
                    horizontal: 'left',
                }}
                open={showCart}
                onClose={() => setShowCart(false)}
                style={{
                    top: "50px"
                }}>

                <CartItems change={cartItems} items={cartItems.map((element) => PRODUCT_LIST[element - 1])} removeFunction={removeFunction} onClose={onClose} />
            </Popover>
                :
                <Popover anchorReference="anchorPosition"
                    anchorPosition={{ top: 0, left: 0 }}
                    anchorOrigin={{
                        vertical: 'top',
                        horizontal: 'left',
                    }}
                    transformOrigin={{
                        vertical: 'top',
                        horizontal: 'left',
                    }}
                    open={showCart}
                    onClose={() => setShowCart(false)}
                    style={{
                        top: "50px"
                    }}>
                    <h6 style={{ color: "red" }}>Add some product in your cart!!!</h6>
                </Popover>}
        </div>
    )
}

export default Products