import { hover } from '@testing-library/user-event/dist/hover'
import React from 'react'
import img from '../img/laptop1.jpeg'

const Product = ({ laptop, addFunction }) => {
    const handleClick = () => {
        console.log("From Prouct")
        addFunction(laptop.id);
    }
    return (
        <div style={{
            display: "flex",
            flexDirection: "column",
            border: "1px solid blue",
            padding: "10px",
            width: "180px",
            height: "400px",
            alignItems: "center",
            justifyContent: "space-between",
            justifyItems: "center"
        }}>
            <img src={img} alt=""
                style={{
                    width: "180px",
                    height: "150px",
                    content: "fit"
                }} />
            <h3>{laptop.title}</h3>

            <table style={{
                textAlign: "left",
                border: "1px solid"
            }}>
                <tbody>
                    <tr >
                        <td >Ram </td>
                        <td >: </td>
                        <td>{laptop.ram}</td>
                    </tr>
                    <tr>
                        <td >Storage </td>
                        <td >: </td>
                        <td>{laptop.storage}</td>
                    </tr>
                    <tr>
                        <td >Processor </td>
                        <td >: </td>
                        <td>{laptop.processor}</td>
                    </tr>
                    <tr>
                        <td >Display </td>
                        <td >: </td>
                        <td>{laptop.display}</td>
                    </tr>
                    <tr>
                        <td >Price </td>
                        <td >: </td>
                        <td style={{ color: "blue", fontWeight: "600" }}>{laptop.price} tk</td>
                    </tr>
                </tbody>
            </table>
            <div>
                <button onClick={handleClick} style={{
                    padding: "5px",
                    backgroundColor: "wheat",
                    cursor: "pointer",
                    borderRadius: "15px",
                    fontSize: "15px",
                    border: "5px"
                }}>Add to Cart</button>
            </div>
        </div>
    )
}

export default Product