import React from 'react'

const CartItem = ({ index, item, removeFunction }) => {
    return (
        <tr>
            <td>{index}</td>
            <td>{item.title}</td>
            <td>{item.price} tk</td>
            <td><button onClick={() => removeFunction(index)}>Remove</button></td>
        </tr>
    )
}

const CartItems = ({ index, items, removeFunction, onClose }) => {

    console.log("cart items", items);
    return (
        <div>
            <table>
                <tr>
                    <th>Sl.</th>
                    <th>Item</th>
                    <th>Price</th>
                    <th><button style={{ color: "red", cursor: "pointer" }} onClick={onClose}>X</button></th>
                </tr>
                {
                    items.map((value, index) => <CartItem key={index} index={index} item={value} removeFunction={removeFunction} />)
                }
            </table>
        </div>
    )
}

export default CartItems